.header_tour {
  $height: 80px;

  position: fixed;
  z-index: 30;
  left: 0;
  right: 0;
  top: 0;
  background: linear-gradient(180deg, #000 0%, rgba(0,0,0,0) 100%);
  height: $height;
  transition: all .22s linear;
  transform: translateY(-100%);

  .isLoaded & {
    transform: translateY(0%);
  }

  .single-virtual-tour & {
    background-color: #000;

  }

  &.scroll {
    background-color: #000;

    .header_tour__inner {
      //position: fixed;
      //left: 0;
      //right: 0;
      //top: 0;
      //z-index: 3;
    }

  }

  &__inner {
    height: $height;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 25px;
    background-color: transparent;
    position: relative;
    z-index: 3;
    transition: all .16s linear;

    &__right {
      display: flex;
      align-items: center;
    }

    .menu__active & {
      background-color: #000;
    }

  }

  &__burger {
    $size: 48px;

    width: $size;
    height: $size;
    background-color: $red;
    border-radius: 50%;
    position: relative;
    cursor: pointer;

    span {
      $width: 18px;

      position: absolute;
      background-color: #fff;
      width: $width;
      height: 1px;
      left: 50%;
      margin-left: -#{$width/2};
      top: 50%;
      transition: all .16s linear;

      &:nth-child(1) {
        margin-top: -5px;

        .menu__active & {
          transform: rotate(45deg);
          margin-top: 0px;
        }

      }
      &:nth-child(2) {

        .menu__active & {
          width: 0px;
        }

      }
      &:nth-child(3) {
        margin-top: 5px;

        .menu__active & {
          transform: rotate(-45deg);
          margin-top: 0px;
        }

      }

    }


  }

  &__breadcrumbs {
    color: #fff;
    margin-left: auto;
    font-size: 18px;
    line-height: 22px;
    font-family: $font-sans;
    padding: 0 32px 0 0;
    font-weight: 300;
    transition: opacity .14s linear;

    @include media(md) {
      display: none;
    }

    &.hide {
      opacity: 0;
    }

    span {
      font-weight: 600;
    }

  }

}




