body
{
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  font-family: $font-serif;
  color: $black;
  background-color: $white;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  width: 100%;
  height: 100%;

  &.no-scroll{
    overflow: hidden;
  }
}

.popup-active
{
  overflow: hidden;
}

.container-fluid {
  $padding: 44px;
  width: 100%;
  max-width: calc(#{$container-width} + #{$padding * 2});
  margin-left: auto;
  margin-right: auto;
  padding-left: $padding;
  padding-right: $padding;

}


main
{

}

section
{
  margin-bottom: 80px;

  @include media(lg)
  {
    margin-bottom: 40px;
  }

  &:last-child
  {
    margin-bottom: 120px;

    @include media(lg)
    {
      margin-bottom: 40px;
    }
  }
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  font-weight: normal;
  color: $black;
  margin: 0 0 35px 0;
  @include media( md ) {
    margin: 0 0 20px 0;
  }
}

h1, .h1 {
  font-family: $font-serif;
  font-weight: 300;
  letter-spacing: -0.6px;
  line-height: 68px;
  font-size: 60px;

  @include media(lg) {
    font-size: 42px;
    line-height: 50px;
  }

}

h2, .h2 {
  font-family: $font-serif;
  font-size: 42px;
  line-height: 54px;

  @include media(lg) {
    font-size: 34px;
    letter-spacing: -0.2px;
    line-height: 48px;
  }
}

h3, .h3 {
  font-family: $font-sans;
  font-size: 32px;
  font-weight: 500;
  letter-spacing: -0.6px;
  line-height: 38px;
  color: $blue;

  @include media(lg) {
    font-size: 24px;
    letter-spacing: -0.2px;
    line-height: 32px;
  }
}

h4, .h4 {
  font-family: $font-sans;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  margin: 0 0 28px 0;

  @include media(lg) {
    font-size: 18px;
    line-height: 24px;
  }
}

h5, .h5 {
  font-family: $font-sans;
  font-size: 18px;
  font-weight: 600;
  line-height: 24px;
  margin: 0 0 22px 0;
}

h6, .h6 {
  font-family: $font-sans;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  margin: 0 0 15px 0;
}

b,
strong
{
  font-weight: bolder;
}

small, .small
{
  font-size: 0.875em;
}

sub,
sup
{
  position: relative;
  font-size: 0.75em;
  line-height: 0;
  vertical-align: baseline;
}

sub
{
  bottom: -.25em;
}

sup
{
  top: -.5em;
}

img,
svg
{
  vertical-align: middle;
}

p {
  font-family: $font-sans;
  color: $black;
  font-size: 16px;
  line-height: 24px;
  padding: 0 0 20px 0;
}

a {
  color: $red;
  text-decoration: none;
  &:hover{
    text-decoration: underline;
  }
}

.fadein
{
  @include fadehide;

  transform: translate3d(0, 2rem, 0);
  transition: 0.5s $transition;
  transition-delay: 0.1s;

  @include media(lg)
  {
    transform: translate3d(0, 0, 0);
  }

  &.fadeleft
  {
    transform: translate3d(-2rem, 0, 0);

    @include media(lg)
    {
      transform: translate3d(0, 0, 0);
    }
  }

  &.faderight
  {
    transform: translate3d(2rem, 0, 0);

    @include media(lg)
    {
      transform: translate3d(0, 0, 0);
    }
  }

  &.fadedelay-2
  {
    transition-delay: 0.225s;
  }

  &.fadedelay-3
  {
    transition-delay: 0.35s;
  }

  &.fadedelay-4
  {
    transition-delay: 0.475s;
  }

  &.fadedelay-5
  {
    transition-delay: 0.6s;
  }

  &.fadedelay-6
  {
    transition-delay: 0.725s;
  }

  &.fadedelay-7
  {
    transition-delay: 0.85s;
  }

  &.fadedelay-8
  {
    transition-delay: 0.925s;
  }

  &.onscreen
  {
    @include fadeshow;
  }

  &:not(.onscreen)
  {
    transition-duration: 0s;
    transition-delay: 0s;
  }
}

.hidden {
  display: none;
}

.wpcf7 {
  margin: 80px auto 120px;
  max-width: 792px;
  font-family: $font-sans;

  @media (max-width: 860px) {
    margin-left: 44px;
    margin-right: 44px;
    max-width: 100%;
    }

  .title {
    margin-bottom: 24px;
    }

  .description {
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 32px;

    @include media(sm) {
      font-size: 16px;
      line-height: 24px;
      }
    }

  hr {
    border-top: 1px solid $light-grey;
    margin-bottom: 48px;
    }

  button {
    opacity: 1;
    transition: opacity 0.2s $transition;

    &:hover {
      opacity: 0.8;
      }
    }

  .message {
    margin-bottom: 40px;

    .message-info,
    .message-success,
    .message-error {
      display: block;
      background: $grey;
      position: relative;
      font-size: 14px;
      line-height: 20px;
      padding: 15px 48px 15px 52px;
      margin-bottom: 24px;

      .message-icon {
        position: absolute;
        top: 50%;
        left: 18px;
        width: 18px;
        height: 18px;
        transform: translateY(-50%);
        }

      .message-button {
        position: absolute;
        background: $dark;
        top: 0;
        right: 0;
        bottom: 0;
        width: 48px;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
          width: 10px;
          height: 10px;
          }
        }
      }
    }

  .field-container {
    margin-bottom: 24px;

    label {
      display: flex;
      color: $dark-grey;
      font-size: 12px;
      line-height: 16px;
      margin-bottom: 8px;
      }

    input, textarea {
      display: flex;
      width: 100%;
      color: $black;
      background: $grey;
      font-size: 14px;
      line-height: 20px;
      padding: 16px 8px;
      border: none;
      border-bottom: 1px solid $light-grey;
      }

    .input-wrapper {
      display: block;
      position: relative;

      .error-icon {
        position: absolute;
        display: none;
        top: 0;
        right: 16px;
        bottom: 0;
        width: 20px;
        justify-content: center;
        align-items: center;
        }
      }

    .field-description {
      font-size: 16px;
      line-height: 24px;
      padding-top: 16px;
      margin-bottom: 24px;

      small {
        font-size: 12px;
        line-height: 16px;
        }

      &+ .file-wrapper {
        margin-bottom: 16px;
        }
      }

    .field-helper {
      color: $dark-grey;
      font-size: 12px;
      line-height: 16px;
      margin-top: 8px;

      &.error {
        color: $error;
        }
      }

    .checkmark {
      position: absolute;
      top: 0;
      left: 0;
      height: 16px;
      width: 16px;
      border: 2px solid $black;
      border-radius: 2px;

      &:after {
        content: "";
        position: absolute;
        display: none;
        }
      }

    .checkbox-wrapper {
      display: block;
      position: relative;
      padding-left: 25px;
      margin-bottom: 12px;
      cursor: pointer;
      font-size: 14px;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;

      input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;

        &:checked ~ .checkmark {
          background-color: $black;
          border-color: $black;
          }

        &:checked ~ .checkmark:after {
          display: block;
          }
        }

      .checkmark:after {
        left: 4px;
        top: 1px;
        width: 5px;
        height: 8px;
        border: solid $white;
        border-width: 0 2px 2px 0;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        }

      &:hover input ~ .checkmark {
        background-color: $light-grey;
        }
      }

    .radiomark {
      position: absolute;
      top: 0;
      left: 0;
      height: 18px;
      width: 18px;
      border: 2px solid $black;
      border-radius: 50%;

      &:after {
        content: "";
        position: absolute;
        display: none;
        }
      }

    .radio-wrapper {
      display: block;
      position: relative;
      padding-left: 25px;
      margin-bottom: 12px;
      cursor: pointer;
      font-size: 14px;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;

      input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;

        &:checked ~ .radiomark {
          background-color: $white;
          border-color: $black;
          }

        &:checked ~ .radiomark:after {
          display: block;
          }
        }

      .radiomark:after {
        top: 3px;
        left: 3px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: $black;
        }

      &:hover input ~ .radiomark {
        background-color: $light-grey;
        }
      }

    .file-wrapper {
      display: block;
      position: relative;
      margin-bottom: 16px;

      .file-placeholder {
        display: block;
        background: $grey;
        color: $placeholder;
        font-size: 14px;
        line-height: 20px;
        padding: 14px 16px;

        button {
          position: absolute;
          background: $dark;
          top: 0;
          right: 0;
          bottom: 0;
          width: 48px;
          z-index: 2;
          display: flex;
          justify-content: center;
          align-items: center;

          svg {
            width: 20px;
            height: 20px;
            transform: rotate(180deg);
            }
          }
        }

      input {
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        opacity: 0;
        z-index: 1;
        }
      }

    .attachment {
      display: block;
      position: relative;
      padding: 16px 14px;
      color: $white;
      font-size: 14px;
      line-height: 20px;
      background: $dark;
      border: 2px solid transparent;
      max-width: 40%;

      @include media(sm) {
        max-width: 100%;
        }

      &:not(:last-child) {
        margin-bottom: 16px;
        }

      &.error {
        color: $black;
        background: none;
        border: 2px solid $error;
        }

      .attachment-info {
        position: absolute;
        display: inline-flex;
        align-items: center;
        top: 0;
        right: 34px;
        bottom: 0;
        width: 12px;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
          width: 12px;
          height: 12px;
          }
        }

      .attachment-action {
        position: absolute;
        display: inline-flex;
        align-items: center;
        top: 0;
        right: 16px;
        bottom: 0;
        width: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        svg {
          width: 8px;
          height: 8px;
          }
        }
      }

    &.textarea {
      label {
        display: flex;
        justify-content: space-between;
        }
      }

    &.file {
      .field-description {
        margin-bottom: 16px;
        }
      }

    &.error {
      .input-wrapper .error-icon {
        display: flex;
        }

      .field-helper {
        color: $error;
        }
      }
    }

  .btn-submit {
    color: $white;
    background: $red;
    padding: 16px;
    border-radius: 40px;
    margin-top: 16px;
    }
  }



