.navigation {
  text-align: center;
  padding: 33px 0 33px 0;

  .screen-reader-text {
    display: none;
  }

  .next,
  .prev {
    &::after {
      content: '';
      position: absolute;
      border: 5px solid transparent;
    }
  }
  .prev {
    &::after {
      border-right: 6px solid $black;
    }
  }
  .next {
    &::after {
      border-left: 6px solid $black;
    }
  }

  .nav-links {
    @include _flexbox();
    @include _justify-content(center);
  }

  .page-numbers {
    $size: 48px;
    color: $black;
    font-size: 12px;
    font-family: $font-sans;
    width: $size;
    height: $size;
    @include _flexbox();
    @include _justify-content(center);
    @include _align-items(center);
    position: relative;

    &.current {
      &::after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        width: 16px;
        height: 4px;
        background-color: #0033A0;
        left: 50%;
        margin-left: -8px;
      }
    }

  }

}



