.map_detail {
  $height-header: 60px;
  $header: 80px;

  position: fixed;
  z-index: 10;
  top: $header;
  bottom: 0;
  width: 50%;
  transition: width .14s linear;
  right: 0;
  background-color: #fff;
  transform: translateX(100%);

  @include media(md) {
    width: 100%;
  }

  &__similar {
    position: fixed;
    top: $header;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 9;
    background-color: transparent;
    display: block;
    transform: translateX(100%);

    .tour__read & {
      transform: translateX(0%);
    }

  }

  .tour__read & {
    transform: translateX(0%);
  }

  &__builds {
    display: none;
    padding: 90px 0 50px 0;

    .full & {
      display: block;
    }

  }

  &.full {
    width: 100%;

  }

  &__header {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    border-bottom: 2px solid #000;
    z-index: 6;
    @include media(md) {
      left: 44px;
      right: 44px;
    }

    &__inner {
      margin: 0 30px;
      font-size: 14px;
      font-family: $font-sans;
      justify-content: center;
      height: $height-header;
      display: flex;
      align-items: center;
      position: relative;

      @include media(md) {
        margin: 0;
      }

      .full & {
        max-width: 980px;
        margin: 0 auto;
      }


    }

    a {
      color: $black;
      display: inline-flex;
      align-items: center;
      height: $height-header;
    }

    .nav{
      &-prev,
      &-next {
        position: absolute;

        &::after {
          $size: 10px;

          content: '';
          display: block;
          width: $size;
          height: $size;
          border: 2px solid $black;
          border-right: none;
          border-bottom: none;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          margin-top: -5px;

        }

      }

      &-prev {
        left: 0;
        padding-left: 22px;

        @include media(md) {
          left: 15px;
        }

        &::after {
          transform: rotate(-45deg);
          left: 0;
        }

      }

      &-next {
        right: 0;
        padding-right: 22px;

        @include media(md) {
          right: 15px;
        }

        &::after {
          transform: rotate(135deg);
          right: 0;
        }

      }

      &-back {
        @include media(md) {
          position: absolute;
          left: 0;
          bottom: -62px;
          color: $red;
          width: 100%;
          right: 0;
          background-color: #fff;
        }
      }

    }

  }

  &__expand {
    position: absolute;
    width: 24px;
    top: $height-header;
    bottom: 0;
    background-color: #E0E0E0;
    left: 0;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color .12s linear;

    @include media(md) {
      display: none;
    }

    &:hover {
      background-color: #000;

      &::after {
        border-color: #fff;
      }

    }

    &::after {
      $size: 10px;

      content: '';
      width: $size;
      height: $size;
      display: block;
      position: relative;
      border: 2px solid $black;
      border-right: none;
      border-bottom: none;
      transform: rotate(-45deg);
      right: -2px;
      transition: all .12s linear;

      .full & {
        transform: rotate(135deg);
        right: 1px;
      }

    }

  }

  &__content {
    position: absolute;
    z-index: 4;
    top: $height-header;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: auto;
    padding: 40px 0px 40px 35px;
    margin: 0 16px 0 0;

    @include media(md) {
      padding: 90px 0 0 0;
      margin: 0;
    }

    &.preloader {
      background: #fff url('../images/preloader.svg') center center no-repeat;
    }

    &__title {
      font-size: 42px;
      line-height: 1.3;
      font-weight: 400;
      @include media(md) {
        font-size: 34px;
      }
    }

    &__location {
      position: relative;
      min-height: 32px;
      padding: 0 0 0 40px;
      font-family: $font-sans;
      font-size: 16px;
      margin: 0 0 22px 0;

      span {
        $size: 32px;

        height: $size;
        width: $size;
        background-color: $red;
        color: #fff;
        font-size: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 0;
        top: -2px;
        border-radius: 50%;

      }

    }

    .newssingle__content-inner {

      .full & {
        max-width: 590px;
      }

    }

    &::-webkit-scrollbar-button {


      @include media_min(md) {
        background-image:url('');
        background-repeat:no-repeat;
        width:8px;
        height:0px;
      }


    }

    &::-webkit-scrollbar-track {

      @include media_min(md) {
        background-color: #fff;
        box-shadow: 0px 0px 0px #000 inset;
      }
    }

    &::-webkit-scrollbar-thumb {

      @include media_min(md) {
        border-radius: 12px;
        background-color: #A8A8A8;
        box-shadow: 0px 0px 0px #000 inset;
        background-position: center;
        background-repeat: no-repeat;
      }

    }

    &::-webkit-resizer {

      @include media_min(md) {
        background-image: url('');
        background-repeat: no-repeat;
        width: 7px;
        height: 0px;
      }

    }

    &::-webkit-scrollbar {
      @include media_min(md) {
        width: 11px;
      }
    }

    .newssingle__content-inner .wp-block-image::after {
      display: none;
    }

  }

  .newssingle {
    border: none;
    padding: 0;

    .wp-swiper .wp-block-image {
      padding-bottom: 0;
    }

  }

}



