.search_page {

  &__panel {
    @include _flexbox();
    @include _justify-content(space-between);
    margin: 0 0 65px 0;
    @include media(sm) {
      margin: 0 0 40px 0;
    }

    @media (max-width: 360px) {
      @include _flex-direction(column);
    }


    &__count {
      font-size: 16px;
      font-family: $font-sans;
    }

    &__sorting {
      position: relative;
      font-family: $font-sans;

      &.active {
        ul {
          display: block;
        }
        .drop-link {
          color: $black;
          &::after{
            transform: rotate(180deg);
            top: -2px;
          }
        }
      }

      .drop-link {
        color: $red;
        position: relative;
        text-decoration: none;
        @include _flexbox();
        @include _align-items(center);

        &::after{
          content: '';
          display: inline-block;
          border: 5px solid transparent;
          border-top: 6px solid #000;
          margin: 0 0 0 14px;
          position: relative;
          top: 3px;
        }

      }

      ul {
        position: absolute;
        background-color: #fff;
        right: 0;
        top: 100%;
        margin-top: 5px;
        text-align: right;
        box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.15);
        display: none;

        > li {

          &.active{
            > a {
              background-color: rgba(0, 0, 0, .04);
              color: $black;
            }
          }

          > a {
            display: block;
            text-decoration: none;
            padding: 12px 15px;
            white-space: nowrap;

            &:hover {
              background-color: rgba(0, 0, 0, .04);
              color: $black;
            }

          }

        }

      }



    }

  }

}



