.date_picker {
  $width: 288px;

  width: $width;
  position: absolute;
  left: 50%;
  margin-left: -#{$width/2};
  top: 100%;
  margin-top: 21px;
  z-index: 999;

  .ui {

    &-input {
      width: 100%;
      height: 50px;
      padding: 0 10px;
      border-bottom: 1px solid #E0E0E0;
      background: transparent url('../images/montch-filter-calendar-black.svg') 95% center no-repeat;
      background-size: 12px;
      color: $black;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;

      &::placeholder {
        color: $black
      }

    }

    &-datepicker {
      background-color: #F4F4F4;
      width: 100% !important;
      left: 0 !important;
      top: 0 !important;
      padding: 50px 0px 3px 0px;
      box-shadow: 0 3px 8px 0 rgba(0,0,0,0.3);
      position: relative !important;

      &-header {
        height: 50px;
        @include _flexbox;
        @include _align-items(center);
        @include _justify-content(center);
        width: 100%;
        padding: 0;
      }

      &-next,
      &-prev {
        $size: 30px;

        width: $size;
        height: $size;
        top: 50%;
        margin-top: -#{$size/2};

        &::after {
          $size: 9px;

          content: '';
          display: block;
          position: absolute;
          width: $size;
          height: $size;
          border: 1px solid $black;
          border-right: none;
          border-bottom: none;
          top: 50%;
          margin-top: -4px;
        }

        span {
          display: none;
        }

      }

      &-next {
        &::after {
          transform: rotate(135deg);
          left: 8px;
        }
      }

      &-prev {
        &::after {
          transform: rotate(-45deg);
          left: 13px;
        }
      }

      &-title {
        font-family: $font-sans;
        color: $black;
        font-size: 14px;
        line-height: 14px;
        font-weight: 700;
      }
      th, td {
        font-size: 14px;
        color: $black;
        font-family: $font-sans;
        text-align: center;
      }
      th {
        padding: 8px 8px;
        font-weight: 700;

        &:first-child {

        }

        &:last-child {

        }

      }

      td {

        a {
          padding: 10px 8px;
          color: $black;
          text-align: center;
          position: relative;

          &.ui-state-active {
            color: $blue;

            &::after {
              $size: 4px;

              content: '';
              display: block;
              position: absolute;
              bottom: 0;
              width: $size;
              height: $size;
              border-radius: 50%;
              left: 50%;
              margin-left: -#{$size/2};
              background-color: $blue;
            }

          }

        }

      }

    }

  }
}






