.tippy {

  &-box {
    width: 282px;
    background-color: #fff;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, .25);
    border-radius: 0;
    padding: 16px;
  }

  &-arrow {
    $size: 16px;

    width: $size;
    height: $size;
    color: #fff
  }

  &__tooltip {

    &__title {
      display: block;
      margin: 0 0 25px 0;
      font-size: 24px;

      &.nomarg {
        margin: 0;
      }

    }

    &__link {
      font-size: 14px;
      font-family: $font-sans;
    }

  }

}



