.megamenu {
    position: fixed;
    z-index: 10;
    left: 0;
    right: 0;
    top: 167px;

    .stiky & {
    top: 75px;
  }

  @include media(lg) {
    display: none;
    }

  &__item {
    background-color: #000;
    transition: .3s ease-in, .3s ease-in-out;
    overflow: hidden;
      display: block;
    transform: translate(0, -101%);
    transition-duration: .25s;
    width: 100%;
    opacity: 0;
    position: absolute;

      &:after {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        height: 4px;
        width: 100%;
        background: $blue;
        z-index: 99999;
      opacity: 0;
      transition: 1s ease-in, 0s ease-in-out;
    }

    &--active {
      z-index: 100;
      display: block;
      height: auto;
      opacity: 1;
      transform: translate(0, 0);

      &:after {
        opacity: 1;
      }
    }

    > .container-fluid {
      padding-top: 50px;
      padding-bottom: 50px;
    }

    &__header {
      border-bottom: 1px solid #E0E0E0;
      @include _flexbox();
      @include _justify-content(space-between);
      @include _align-items(center);
      padding: 32px 0;

      .title {
        color: #fff;
        margin: 0;
      }

      .button {
        width: 118px;
        height: 32px;
        padding: 8px 12px;
        font-size: 14px;
        line-height: 14px;
        text-align: left;
        &::after{
          right: 10px;
        }
        &:hover{
          &::after{
            background-image: url('../images/ic_arrow_right_black.svg');
          }
        }
      }

    }

    &__content {
      $right-width: 180px;
      @include _flexbox();
      padding: 32px 0;

      &__left {
        width: calc(100% - #{$right-width});
        padding: 0 50px 0 0;

        > ul {
          display: grid;
          grid-template-rows: 1fr;
          gap: 0px 0px;
          font-size: 16px;
          font-family: $font-sans;


          &.column {
            &-1 {
              grid-template-columns: 1fr;
            }
            &-2 {
              grid-template-columns: 1fr 1fr;

              &.mini {
                max-width: 999px;
              }

            }
            &-3 {
              grid-template-columns: 1fr 1fr 1fr;
            }
          }

          a {
            color: #fff;
            &:hover{
              text-decoration: underline;
            }
          }

          li {

            &:not(:last-child){
              margin: 0 0 6px 0;
            }
          }

          .semibold {

            > a {
              margin: 0 0 24px 0;
              font-weight: 600;
              display: inline-block;
            }

            &.nomargin {
              > a {
                margin: 0 0 7px 0;
              }
            }

          }

        }

      }
      &__right {
        width: $right-width;

        .title {
          color: #fff;
          margin: 0 0 25px 0;
        }

        ul {
          font-size: 14px;
          font-family: $font-sans;
          > li {
            &:not(:last-child) {
              margin: 0 0 8px 0;
            }
            a {
              color: #fff;
              &:hover{
                text-decoration: underline;
              }
            }
          }
        }

      }

    }

  }

}




