.return_tour {
  position: fixed;
  z-index: 5;
  bottom: 0;
  left: 0;
  display: inline-block;
  height: 39px;
  font-size: 14px;
  line-height: 38px;
  color: #fff;
  background: #000 url('../images/tour_icon_return.svg') 84% center no-repeat;
  padding: 0 43px 0px 16px;
  font-family: $font-sans;

  @include media(md) {
    display: none;
  }

  &:hover {
    background-color: $red;
    text-decoration: none;
  }

}



