.search_page {

  &__header {
    border-bottom: 1px solid #E0E0E0;
    margin: 0 0 25px 0;

    &__title {
      font-size: 24px;
      line-height: 1.3;
      font-family: $font-sans;
      font-weight: 500;
      margin: 0 0 18px 0;
    }

    &__subtitle {
      font-size: 32px;
      line-height: 1.3;
      font-family: $font-sans;
      font-weight: 500;
      margin: 0 0 22px 0;
    }

  }

}



