.header_tour {

  &__megamenu {
    background-color: #000;
    position: fixed;
    transition: all .16s linear;
    height: 100vh;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    transform: translateY(-100%);

    .menu__active & {
      transform: translateY(0%);
    }

    &__inner {
      height: 100vh;
      overflow: auto;
      width: 100%;
      transition: opacity 0.3s linear;
      opacity: 0;
      padding: 130px 0 30px 0;

      @include media(md) {
        padding-bottom: 60px;
      }

      .menu__active & {
        transition-delay: .3s;
        opacity: 1;
      }
    }

    &__row {
      margin: 0 0 42px 0;
      @include media(md) {
        margin: 0 0 46px 0;
      }
    }

    &__title {
      font-family: $font-sans;
      color: #fff;
      padding: 0 120px;
      margin: 0 0 19px 0;
      font-size: 32px;

      @include media(md) {
        padding: 0 44px;
        font-size: 24px;
        font-weight: 500;
        letter-spacing: -0.2px;
        line-height: 32px;
      }

    }

    &__slider {

      .swiper{

        &-slide {
          width: 180px;
        }

        &-button{

          &-next, &-prev {
            background-color: $red;
            width: 24px;
            height: 148px;
            top: 50%;
            transform: translateY(-50%);
            margin: 0;
            color: #fff;
            z-index: 3;

            @include media(md) {
              display: flex !important;
              height: 64px;
            }

            &::after {
              font-size: 14px;
            }

          }

          &-prev {
            left: 0;
          }

          &-next {
            right: 0;
          }

        }

        &-container {

          &:hover {
            .similar {
              width: 0;
            }
          }

          .similar {
            display: block;
            position: absolute;
            z-index: 2;
            width: 280px;
            top: 0;
            bottom: 0;
            transition: width .16s linear;
            will-change: width;

            @include media(md) {
              display: none;
            }

            &:hover {
              display: none;
            }

            &__left {
              eft: 0;
              background: -moz-linear-gradient(left,  rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%);
              background: -webkit-linear-gradient(left,  rgba(0,0,0,1) 0%,rgba(0,0,0,0) 100%);
              background: linear-gradient(to right,  rgba(0,0,0,1) 0%,rgba(0,0,0,0) 100%);
              filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#00000000',GradientType=1 );
            }

            &__right {
              right: 0;
              background: -moz-linear-gradient(left,  rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
              background: -webkit-linear-gradient(left,  rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%);
              background: linear-gradient(to right,  rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%);
              filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000',GradientType=1 );
            }

          }

        }


      }

    }

    &__btn {
      display: none;
      padding: 24px 44px 0 44px;

      @include media(md) {
        display: block;
      }

      .btn {
        &:hover {
          border-color: #fff;
          background-color: #fff;
          color: $black;

          &::after {
            background-image: url('../images/ic_arrow_right_black.svg');
          }


        }
      }

    }

    &__footer {
      display: none;
      border-top: 1px solid #fff;
      margin: 65px 44px 0 44px;

      @include media(md){
        display: block;
      }

      a {
        display: block;
        text-decoration: none;
        font-size: 14px;
        font-family: $font-sans;
        color: #fff;
        padding: 14px 0;
        background: url('../images/tour_icon_return.svg') 100px center no-repeat;

        @include media(md) {
          padding-bottom: 0;
        }

      }

    }

  }

}



