.faculty_section {
  @import "taxfilter_section";
  margin: 40px 0 0 0;
  .taxfilter_section {
    &__filter {
      z-index: 15;

      &-list {

        &--select {
          padding: 0 12px;
          width: 100%;

          @include media(md) {
            width: 100%;

            .select2-container,
            .select2-dropdown {
              position: relative !important;
              top: 100% !important;
              }
            }

          &---row {
            margin: 0 -12px;

            .col {
              width: 25%;
              padding: 0 6px;
              float: left;
              @include media(md) {
                width: 100%;
                float: none;
                margin: 0;
                }
              }
            }

          }
        }
      }

    &__inner {
      padding: 78px 0 0 0;
      border-top: 2px solid #000;
      }

    &__title {
      margin: 0 0 20px 0;
      color: $black;
      }

    &__panel {
      margin: 0 0 30px 0;

      &__row {
        margin: 0 0 24px 0;
        @include _flexbox();
        @include _justify-content(space-between);
        }

      &__col {
        width: calc(25% - 15px);
        }

      .search {
        position: relative;

        input {
          @extend .input_search;
          }

        [type="submit"] {
          @extend .button_search_submit;
          }

        }

      &__badges,
      .taxfilter_section__panel-badges {
        width: 100%;

        > ul {
          > li {
            &:last-child {
              margin-bottom: 10px;
              }
            }
          }

        }

      }

    &__table {
      $width-column: 25%;
      $border-color: #E0E0E0;
      $padding-right: 120px;

      &__header {
        @include _flexbox();
        border-top: 1px solid $border-color;
        border-bottom: 1px solid $border-color;
        font-size: 12px;
        line-height: 12px;
        white-space: nowrap;
        font-family: $font-sans;
        padding: 0 $padding-right 0 0;

        > li {
          width: $width-column;
          padding: 17px 0;

          &:first-child {
            padding-left: 8px;
            }

          }

        }

      &__list {

        > li {
          border-bottom: 1px solid $border-color;
          }

        &__item {
          font-family: $font-sans;
          font-size: 18px;
          line-height: 1.4;
          color: $black;
          text-decoration: none;
          padding: 18px $padding-right 18px 0;
          @include _flexbox();
          position: relative;

          > * {
            width: $width-column;
            padding-right: 20px;

            }

          &:hover {
            text-decoration: none;
            background-color: #F4F4F4;

            .arrow {
              opacity: 1;
              }

            }

          .name {
            font-weight: 600;
            padding: 0 0 0 8px;
            }

          .arrow {
            padding: 0;
            position: absolute;
            right: 16px;
            top: 50%;
            width: auto;
            transform: translateY(-50%);
            font-size: 14px;
            font-weight: 600;
            transition: all .1s ease-in;
            @include _flexbox();
            @include _align-items(center);
            opacity: 0;

            &::after {
              content: "";
              display: block;
              width: 20px;
              height: 16px;
              background: url("../images/ic_arrow_right_red.svg") center center no-repeat;
              margin: 0 0 0 12px;
              background-size: contain;

              }

            }

          }

        }

      }
    }
  .taxfilter_section__filter-list--input {
    width: 100%;
    padding: 0;
  }

}


.faculty_section .taxfilter_section__filter-list--select {
  width: 100%;
  }



