.posts_row {

  &__inner {
    @include media(lg) {
      position: relative;
    }

    @include media(md) {
      padding-bottom: 80px;
    }

  }

  &__head {
    padding-top: 80px;
    border-top: 2px solid $dark;

    @include _flexbox;
    @include _align-items(center);
    @include _justify-content(space-between);
    @include _flex-wrap(wrap);

    margin: 0 0 40px 0;

    @include media(lg)
    {
      padding-top: 40px;
    }
  }

  &__title {
    margin: 0;
  }

  &__link {
    @include media(md)
    {
      position: absolute;
      bottom: 0;
      left: 0;
      margin: 0;
    }


  }

  .news_events{

    &__list_row {
      @include _flexbox();
      @include _justify-content(space-between);
      @include _align-items(stretch);
      @include _flex-wrap(wrap);
      margin: 0 0 24px 0;

      .news_events__item {
        width: calc(50% - 12px);

        @include media(sm) {
          width: 100%;
        }
      }

    }

    &__list_grid {
      position: relative;
      margin: 0 -12px;

      @include media(sm) {
        height: auto !important;
      }

      .grid-sizer,
      .newseventsMasonry-item{
        width: 33.3%;

        @include media(md) {
          width: 50%;
        }

        @include media(sm) {
          width: 100%;
        }

      }

      .grid-sizer {
        @include media(sm) {
          display: none;
        }
      }

      .newseventsMasonry-item{
        @include media(sm) {
          position: static !important;
        }
      }

      .news_events__item {
        width: 100%;
        margin-bottom: 24px;
      }

      .newseventsMasonry-item {
        padding: 0 12px;
      }

    }



    &__item {

      &__title {
        margin-bottom: 10px;
      }

      &__date {
        font-size: 14px;
        font-weight: 600;
        color: #161616;
        margin: 0 0 8px 0;
      }

      &__place,
      &__author {
        position: relative;
        margin: -8px 0 8px 0;
      }

      &__cat {
        margin-top: 8px;
      }

      &__custom {
        background-color: $blue_indigo;
        margin-bottom: 24px;
        padding: 24px;

        .title {
          color: #B0E8FE;
          margin: 0 0 22px 0;
          font-weight: 400;
        }

        .text {
          font-size: 16px;
          line-height: 24px;
          color: #fff;
        }

        .link {
          padding: 19px 0 10px 0;

          a {
            color: #fff;

            &::after {
              background-image: url('../images/ic_arrow_right.svg');
            }

          }

        }

      }

    }

  }

  &__categories {

    &__title {
      margin: 0 0 18px 0;
    }

    &__list {
      @include _flexbox();
      @include _flex-wrap(wrap);
      font-family: $font-sans;

      > li {
        margin: 0 8px 8px 0;
      }

      a {
        display: block;
        text-decoration: none;
        height: 48px;
        color: #fff;
        font-size: 14px;
        line-height: 14px;
        padding: 16px;
        background-color: $blue_indigo_2;
        border-radius: 999px;

        &:hover {
          background-color: #00226B;
        }

      }

    }

  }


}



