/**
 * Classes Link
 *
 * link
 * link-red
 * link-white
 * link-black
 *
 */

.link {
  font-family: $font-sans;
  font-size: 14px;
  line-height: 18px;
  color: $black;
  font-weight: 500;
  position: relative;

  &:hover{

  }

  &:not(.btn){
    &:hover{
      text-decoration: underline;
      color: $red;

    }
  }

  &.arrow {
    @include _flexinline();
    @include _align-items(center);

    &::after {
      content: '';
      display: block;
      width: 20px;
      height: 16px;
      background: url('../images/ic_arrow_right_black.svg') center center no-repeat;
      background-size: contain;
      margin: 0 0 0 17px;
      position: relative;
      transition: transform 0.15s linear;
      will-change: transform;
    }

    &:hover {
      &::after {
        transform: translateX(8px);
      }
    }

    &-red {
      &::after {
        background-image: url('../images/ic_arrow_right_red.svg');
      }
    }

  }

  &-white{
    color: #fff;

    &.arrow{
      &::after{
        background-image: url('../images/ic_arrow_right.svg');
      }
    }

    &:hover{
      color: #fff !important;
    }

  }

  &-black{

  }

}



