.cards_tour {
  $background-blue-indigo: #0A2F48;
  $background-blue-light: #E5FAFF;

  padding: 140px 0 0 0;
  position: relative;
  z-index: 1;
  background-color: $background-blue-indigo;
  background-image: url('../images/logo-tour-big.svg');
  background-size: 40%;
  background-attachment: fixed;
  background-position: 99% bottom;
  background-repeat: no-repeat;

  @include media(lg) {
    padding: 80px 0 0 0;
  }
  @include media(md) {
    padding: 40px 0;
    background-attachment: inherit;
    background-size: 388px;
    background-position: calc(100% + 130px) 110%;

  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    right: 0;
    height: 200px;
    background-color: $background-blue-light;
    //background-size: 40%;
    //background-attachment: fixed;
    //background-position: 99% bottom;
    //background-repeat: no-repeat;
    //background-image: url('../images/logo-tour-big-light.svg');
    @include media(md) {
      display: none;
    }

  }

  .container-fluid {
    position: relative;
    z-index: 3;
  }

  &__inner {
    display: flex;
    position: relative;

    @include media(md) {
      flex-direction: column;
    }

    &__left,
    &__right {
      width: 50%;
      @include media(md) {
        width: 100%;
      }
    }

    &__left {

    }

    &__right {
      padding: 0 0 0 60px;
      @media (max-width: 1360px) {
        padding: 0 0 0 30px;
      }
      @include media(md) {
        padding: 0;
      }
    }

  }

  &__subtitle {
    color: #fff;
    margin: 0 0 22px 0;
    @include media(md) {
      margin: 0 0 20px 0;
    }
  }

  &__text {
    max-width: 490px;
    border-top: 8px solid #fff;
    padding: 76px 0 0 0;
    margin: 0 0 130px 0;
    @include media(lg) {
      padding: 30px 0 0 0;
    }
    @include media(md) {
      padding: 40px 0 0 0;
      margin: 0 0 40px 0;
    }
    //position: sticky;
    //top: 100px;

    &__subtitle {
      color: #B0E8FE;
      font-weight: 600;
      font-family: $font-sans;
      margin: 0 0 19px 0;
      @include media(md) {
        margin: 0 0 14px 0;
      }
    }

    &__title {
      color: #fff;
      margin: 0 0 22px 0;
      @include media(md) {
        margin: 0 0 20px 0;
      }
    }

    &__desc {
      color: #fff;
      @include media(md) {
        font-size: 20px;
        line-height: 1.5;
      }
    }

    &__btn {
      padding: 30px 0 0 0;
    }

  }

  &__list {
    $padden: 12px;
    $padden-lg: 7px;

    display: flex;
    flex-wrap: wrap;
    margin-left: -#{$padden};
    margin-right: -#{$padden};
    @include media(lg){
      margin-left: -#{$padden-lg};
      margin-right: -#{$padden-lg};
    }
    @include media(md){
      display: none;
    }

    > li {
      display: flex;
      width: 33.32%;
      padding: 0 $padden;
      margin: 0 0 24px 0;
      @include media(lg){
        padding: 0 $padden-lg;
        margin: 0 0 #{$padden-lg*2} 0;
      }
    }

    &__slider {
      $padd: 44px;

      display: none;
      position: relative;
      margin: 0 -#{$padd};

      @include media(md) {
        display: block;
      }

      .swiper{

        &-slide {
          width: 180px;
        }

        &-button{

          &-prev,
          &-next {
            background-color: $red;
            width: 24px;
            height: 64px;
            top: 50%;
            transform: translateY(-50%);
            margin: 0;
            display: flex !important;
            align-items: center;
            justify-content: center;
            color: #fff;

            &::after {
              font-size: 14px;
            }

          }

          &-prev {
            left: 0;
          }

          &-next {
            right: 0;
          }

        }

      }

    }

    &__item {
      display: block;
      width: 100%;
      height: 100%;
      position: relative;
      z-index: 1;

      &::after {
        content: '';
        display: block;
        position: absolute;
        z-index: 3;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 70%;
        background: linear-gradient(180deg, rgba(0,0,0,0) 0%, #000000 99.96%);
      }

      img {
        position: relative;
        z-index: 2;
        object-fit: contain;
        width: 100%;
        height: 100%;
      }

      span {
        position: absolute;
        z-index: 4;
        bottom: 0;
        left: 0;
        right: 0;
        display: inline-block;
        padding: 20px 16px;
        font-family: $font-sans;
        font-size: 20px;
        line-height: 1.4;
        color: #fff;

        .header_tour__megamenu & {
          font-size: 16px;
          @include media(lg) {
            font-size: 16px;
            }
          @include media(md) {
            font-size: 16px;
            }
          }

        @include media(lg) {
          font-size: 18px;
        }
        @include media(md) {
          font-size: 16px;
        }

        &::after {
          content: '';
          display: block;
          position: absolute;
          transition: height .3s ease-in;
          will-change: height;
          height: 4px;
          background-color: $red;
          left: 0;
          right: 0;
          bottom: 0;
          z-index: -1;
        }

      }

      &:hover {
        span {
          &::after {
            height: 100%;
          }
        }
      }

    }

  }

  &__image {
    display: block;
    width: 100%;
    position: relative;

    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }

    span {
      $size: 48px;
      $position: 16px;

      display: block;
      position: absolute;
      z-index: 3;
      width: $size;
      height: $size;
      border-radius: 50%;
      background: $red url('../images/zoom-in.svg') center center no-repeat;
      background-size: 50%;
      top: $position;
      right: $position;

      @include media(md) {
        $size: 28px;
        $position: 10px;

        width: $size;
        height: $size;
        top: $position;
        right: $position;
      }

    }

  }

  &.black {
    background-color: $background-blue-light;
    padding: 120px 0 0 0;
    background-image: url('../images/logo-tour-big-light.svg');
    @include media(lg) {
      padding: 80px 0 0 0;
    }
    @include media(md) {
      padding: 40px 0;
    }

    &::after {
      background-color: $background-blue-indigo;
      //background-image: url('../images/logo-tour-big.svg');
      height: 100px;

    }


    .cards_tour {


      &__subtitle {
        color: #000;
      }

      &__text {
        border-top-color: $black;

        &__subtitle {
          color: #0A2F48;
        }

        &__title {
          color: $black;
        }

        &__desc {
          color: $black;
        }

        &__btn {
          .btn {
            border-color: $black;
            color: $black;

            &::after {
              background-image: url('../images/ic_arrow_right_black.svg');
            }

            &:hover {
              color: #fff;

              &::after {
                background-image: url('../images/ic_arrow_right.svg');
              }

            }

          }
        }

      }


    }


  }

  &.image {
    padding-bottom: 140px;
    @include media(lg) {
      padding-bottom: 80px;
    }
    @include media(md) {
      padding-bottom: 40px;
    }

    &::after {
      height: 240px;
      @include media(lg) {
        height: 180px;
      }
    }

    .cards_tour {

      &__text {
        border-top-width: 4px;
      }

    }

  }

}



