.search_page {

  &__list {

    &__item {
      border-bottom: 1px solid #E0E0E0;
      padding: 35px 0;

      &:first-child{
        padding-top: 0;
      }

      .title {
        font-weight: 500;
        font-size: 24px;
        line-height: 1.3;
        margin: 0 0 20px 0;
        font-family: $font-sans;
      }

      .url {

      }
      .url,
      .url a{
        color: $black;
        text-decoration: none;
        font-size: 14px;
        line-height: 1.2;
        font-family: $font-sans;
        word-wrap: break-word;
      }

      .summary {

        p {
          padding: 20px 0 0 0;
          margin: 0;
        }
      }

    }

  }

}



