.news_events {

  &__inner {

    @include media(lg) {
      padding-bottom: 90px;
      position: relative;
    }

  }

  &__head
  {
    padding-top: 80px;
    border-top: 2px solid $dark;
    @include _flexbox;
    @include _align-items(center);
    @include _justify-content(space-between);
    @include _flex-wrap(wrap);
    margin: 0 0 40px 0;

    @include media(lg)
    {
      padding-top: 40px;
    }
  }

  &__title{
    margin: 0px;
  }

  &__link
  {
    @include media(md)
    {
      position: absolute;
      bottom: 0;
      left: 0;
      margin: 0;
    }

    @include media(xs)
    {
      //width: calc(100% - 88px);
    }

  }

  &__list
  {
    @include _flexbox;
    @include _flex-wrap(wrap);
    @include _justify-content(space-between);
    @include _align-items(stretch);
  }

  &__item {
    @include _flexbox;
    @include _flex-direction(column);
    width: calc(33.3333% - 12px);
    background-color: #F4F4F4;
    padding: 0 0 8px;
    transition: 0.3s all ease-in;

    &:hover
    {
      background-color: $red;
      text-decoration: none;

      .news_events__item__image
      {
        border-bottom: 4px solid #6B1B1F;
      }

      .news_events__item__title,
      .news_events__item__date,
      .news_events__item__place,
      .news_events__item__author,
      .news_events__item__post
      {
        color: $white;

    }
    }

    @include media(sm)
    {
      width: 100%;
      padding: 16px;
      margin-top: 20px;

      &:first-child {
        margin-top: 0;
      }

    }

    &__image
    {
      width: 100%;
      border-bottom: 4px solid $red;
      transition: 0.3s all ease-in;
    }

    &__info
    {
      padding: 16px;
      @include _flexbox;
      @include _flex-direction(column);
    }

    &__post
    {
      text-transform: capitalize;
      color: $black;
      font-family: $font-sans;
      font-size: 16px;
      font-weight: 600;
      letter-spacing: 0;
      line-height: 1;
      vertical-align: middle;
      transition: 0.3s all ease-in;
      position: relative;
      z-index: 1;

      &:before
      {
        content: '';
        display: inline-block;
        margin-right: 8px;
        height: 32px;
        width: 32px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center center;
        vertical-align: middle;
      }

      &.event
      {
        &:before
        {
          background-image: url("../images/event-icon.svg");
        }
      }

      &.news
      {
        &:before
        {
          background-image: url("../images/news-icon.svg");
        }
      }

      &.blog,
      &.research {
        position: relative;
        line-height: 29px;

        &::after {
          $size: 32px;

          content: "";
          display: block;
          position: absolute;
          z-index: -1;
          width: $size;
          height: $size;
          border-radius: 50%;
          left: 0;
          top: 0;
        }

        &::before {
          $size: 19px;

          width: $size;
          height: $size;
          margin: 1px 15px 0 7px;
        }

      }

      &.blog {

        &::after {
          background-color: $red;
        }

        &::before {
          background-image: url("../images/blog.svg");
        }

      }

      &.research {

        &::after {
          background-color: #B0E8FE;
        }

        &::before {
          background-image: url("../images/research.svg");

        }

      }

    }

    &__title
    {
      margin: 16px 0;

    }

    &__date,
    &__place,
    &__author{
      color: $black;
      transition: 0.3s all ease-in;
      padding: 0;
        font-size: 14px;
        letter-spacing: 0;
        line-height: 20px;

      @include media(lg)
      {

      }
    }

    &__cats
    {
      @include _flexbox;
      @include _align-items(center);
      @include _flex-wrap(wrap);
    }

    &__cat
    {
      margin-top: 10px;
      margin-right: 8px;
      border-radius: 14.5px;
      color: $black;
      font-family: $font-sans;
      background-color: #E0E0E0;
      padding: 7px 16px;
      font-size: 14px;
      letter-spacing: 0;
      line-height: 16px;

      @include media(lg) {
        font-size: 14px;
        letter-spacing: 0;
        line-height: 16px;
      }
    }

    &.background {
      background-color: #F4F4F4;
      padding: 16px;
      position: relative;

      &::after {
        content: '';
        opacity: 0;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 4px;
        background-color: #6B1B1F;
        transition: 0.3s all ease-in;
      }

      .news_events__item{

        &__image {
          border: none;
        }

        &__info {
          padding-left: 0;
          padding-right: 0;
          padding-bottom: 0;
        }

      }

      &:hover {
        background-color: $red;
        text-decoration: none;

        &::after {
          opacity: 1;
        }


      }

    }

  }
}



