html, body {
  height: 100%;
  min-height: 100%;
}
.main {
  min-height: 100vh;
}

.map_tour {
  position: relative;

  &__inner {

    .tour__read & {
      width: 50%;

      @include media(md) {
        width: 100%;
      }

    }

    &__map {
      $height: calc(100vh - 80px);

      position: absolute !important;
      top: 80px;
      right: 0;
      left: 0;
      z-index: 1;
      height: $height;
      min-height: $height;
      transition: all .14s linear;
      bottom: 0;

      @include media(lg) {
        //$height: calc(100vh - 110px);
        //
        //height: $height;
        //min-height: $height;
      }

      .tour__point {

        &-red-marker {
          $size: 54px;

          display: flex;
          justify-content: center;
          position: relative;
          width: $size;
          height: $size;
          border: 2px solid #fff;
          background-color: #cb333b;
          border-radius: 50%;
          font-family: $font-serif;
          font-size: 28px;
          font-weight: 600;
          color: #fff;
          z-index: 2;
          line-height: 1.7;

          &:hover {
            background-color: #000;
            border-color: #fff;

            &::before {
              content: '';
              display: block;
              position: absolute;
              width: $size + 8px;
              height: $size + 8px;
              background-color: rgba(0, 0, 0, .25);
              border-radius: 50%;
              z-index: -1;
              left: 50%;
              top: 50%;
              transform: translateX(-50%) translateY(-50%);

            }

          }

        }

        &-blue-marker {
          $size: 34px;

          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          width: $size;
          height: $size;
          border: 2px solid #fff;
          background-color: #00226B;
          border-radius: 50%;
          z-index: 2;

          &:hover {
            background-color: #000;
            border-color: #000;

            &::before {
              content: '';
              display: block;
              position: absolute;
              width: $size + 6px;
              height: $size + 6px;
              background-color: rgba(0, 0, 0, .25);
              border-radius: 50%;
              z-index: -1;
              left: 50%;
              top: 50%;
              transform: translateX(-50%) translateY(-50%);

            }

          }

        }

      }

      &.map_translation {
        width: 50%;
      }

    }

  }

}



