.intro_tour {
  position: relative;
  z-index: 1;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;

  &::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    opacity: 0.8;
    background: linear-gradient(270deg, rgba(0,0,0,0) 0%, #000000 99.96%);
  }

  .container-fluid {
    position: relative;
    z-index: 2;
  }

  &__inner {
    min-height: 100vh;
    padding: 160px 0 100px 0;
    @include media(md) {
      padding: 120px 0 100px 0;
    }
  }

  &__box {
    max-width: 490px;
    border-top: 8px solid #fff;
    padding: 72px 0 0 0;
    @include media(lg) {
      padding: 30px 0 0 0;
    }
    @include media(md) {
      padding: 35px 0 0 0;
    }
  }

  &__title {
    color: #fff;
    margin: 0 0 28px 0;

    transition: all .2s ease;
    opacity: 0;
    transform: translateY(20px);

    @include media(md) {
      margin: 0 0 15px 0;
    }

    .isLoaded & {
      opacity: 1;
      transform: translateY(0);
      transition-delay: 0.1s;
    }

  }

  &__text {
    color: #fff;

    transition: all .2s ease;
    opacity: 0;
    transform: translateY(20px);

    @include media(md) {
      font-size: 20px;
      line-height: 1.5;
    }

    .isLoaded & {
      opacity: 1;
      transform: translateY(0);
      transition-delay: 0.14s;
    }

  }

  &__scroll {
    position: absolute;
    z-index: 4;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
    color: #fff;
    line-height: 24px;
    text-align: center;
    font-family: $font-sans;
    padding: 30px 0 0 0;
    display: inline-block;
    @include media(md) {
      bottom: 20px;
    }

    &::before {
      $size: 15px;

      content: '';
      display: block;
      position: absolute;
      width: $size;
      height: $size;
      border: 2px solid #fff;
      animation: tour-arrow-down 1.5s infinite;
      border-right: none;
      border-bottom: none;
      top: 0;
      left: 50%;
      margin-left: -8px;
      transform: rotate(-135deg);
    }

    span {
      @include media(md) {
        display: none;
      }
    }

    &:hover {
      text-decoration: none;
    }

  }



}

@keyframes tour-arrow-down {
  0% {
    transform: translate(0) rotate(-135deg);
  }
  20% {
    transform: translateY(7px) rotate(-135deg);
  }
  40% {
    transform: translate(0) rotate(-135deg);
  }
}



